*,
::after,
::before {
    box-sizing: border-box;
}

body {
    font-family: 'Heebo', sans-serif;
    background-image: url('../images/background/bg.png');
}

#main {
    margin: 0 auto;
    text-align: center;
}
.h1 {
    color: rgb(233, 233, 233);
    font-size: 1.75rem;
    font-weight: bold;

    margin: 20px 0 50px 0;
}

/*******INPUT*******/

.input_wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
}

.button_search {
    border: none;
    background: none;
}

.button_search:hover {
    cursor: pointer;
}

.input_search {
    width: 23%;
    border-radius: 0.5rem;
    border: none;
    outline: none;
    margin-right: 5px;
    padding: 0.5rem;
    font-size: 1.1rem;
}

.icon_search {
    width: 25px;
    height: 25px;
}

/*******WEATHER*******/

.main_cards {
    width: 80vw;
    margin: auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    padding-top: 2rem;
}

.card_weather {
    width: 300px;
    height: 175px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: 0 4px 6px 0 hsl(0deg 0% 100% / 25%);
    border-radius: 0.5rem;
    transition: 0.75s ease;

    margin-right: 30px;
}

.card_weather:hover {
    transform: scale(1.075);
}

.button_card_cancel {
    align-self: flex-end;
    cursor: pointer;
    padding-right: 0.5rem;
}

.wrapper {
    width: 280;
    height: 120px;
}

.card_link {
    text-align: left;
    color: #fff;
    align-self: center;
    text-decoration: none;
}

h3 {
    color: #fff;
    font-size: 1.75rem;
    margin-bottom: 0.5rem;
    font-weight: 500;
    line-height: 1.2;
}

.card_stats {
    display: flex;
    justify-content: center;
    align-items: center;
    grid-gap: 2rem;
    gap: 2rem;
}

.card_temp_min,
.card_temp_max {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    grid-gap: 1rem;
    gap: 1rem;
}

p {
    font-size: 18px;
    margin-top: 0;
    margin-bottom: 1rem;
    color: #fff;
}

/*******OPTIONS*******/

.card_options {
    color: #fff;
    width: 250px;
    height: 280px;
    text-align: left;
    padding: 3rem;
    box-shadow: 0 4px 6px 0 hsl(0deg 0% 100% / 25%);
    border-radius: 0.5rem;

    line-height: 1.5;
}

.wrapper_button_switch_back {
    width: 100%;
    text-align: right;
}

.button_switch_back {
    align-self: flex-end;
    cursor: pointer;
    padding-right: 0.5rem;
}
